module.exports = "import { Component } from '@angular/core';\r\n\r\n@Component({\r\n  selector: 'nz-demo-popover-control',\r\n  template: `\r\n    <button\r\n      nz-button\r\n      nzType=\"primary\"\r\n      nz-popover\r\n      nzTitle=\"Title\"\r\n      [(nzVisible)]=\"visible\"\r\n      (nzVisibleChange)=\"change($event)\"\r\n      nzTrigger=\"click\"\r\n      [nzContent]=\"contentTemplate\">\r\n      Click me\r\n    </button>\r\n    <ng-template #contentTemplate>\r\n      <a (click)='clickMe()'>Close</a>\r\n    </ng-template>\r\n  `\r\n})\r\nexport class NzDemoPopoverControlComponent {\r\n  visible: boolean;\r\n\r\n  clickMe(): void {\r\n    this.visible = false;\r\n  }\r\n\r\n  change(value: boolean): void {\r\n    console.log(value);\r\n  }\r\n}\r\n"